import { executeQuery } from "@/lib/db";
import { NextResponse } from "next/server";

export async function GET(request: Request) {
  const { searchParams } = new URL(request.url);
  const email = searchParams.get("email");
  const year = Number(searchParams.get("year"));

  if (!email || !year) return NextResponse.json([]);

  try {
    // 1. Busca os livros (Tabela Principal)
    const booksQuery = `
      SELECT *, total_pages as display_pages 
      FROM public.reading_data 
      WHERE email = $1 AND year = $2
      ORDER BY month ASC, status DESC
    `;
    const books = await executeQuery(booksQuery, [email, year]);

    // 2. Busca os reviews (Tabela de Gênero/Nota) - Se falhar, não trava os livros
    let reviews = [];
    try {
      const reviewsQuery = `
        SELECT br.* FROM public.book_reviews br
        JOIN public.users u ON u.id = br.user_id
        WHERE u.email = $1
      `;
      reviews = await executeQuery(reviewsQuery, [email]);
    } catch (e) {
      console.log("Ainda sem tabela de reviews completa, ignorando...");
    }

    // 3. Junta os dados para o componente ler (Genre, Review, Rating)
    const result = books.map((book: any) => {
      const rev = reviews.find((r: any) => r.title === book.book_name);
      return {
        ...book,
        genre: rev?.genre || "",
        review: rev?.review || "",
        rating: rev?.rating || 0,
        cover_url: rev?.cover_url || book.cover_url
      };
    });

    // RETORNA O ARRAY DIRETO (O que o seu componente espera)
    return NextResponse.json(result);

  } catch (error: any) {
    console.error("Erro na API:", error);
    return NextResponse.json([]); // Retorna vazio mas não quebra a tela
  }
}

export async function POST(request: Request) {
  try {
    const body = await request.json();
    const { action, email, bookName, rating, coverUrl, totalPages, review, genre } = body;

    if (action === "UPDATE_REVIEW") {
      const userRes = await executeQuery("SELECT id FROM public.users WHERE email = $1", [email]);
      if (!userRes.length) throw new Error("Usuário não encontrado");

      const upsertQuery = `
        INSERT INTO public.book_reviews (user_id, title, rating, cover_url, total_pages, review, genre)
        VALUES ($1, $2, $3, $4, $5, $6, $7)
        ON CONFLICT (user_id, title) 
        DO UPDATE SET 
          rating = EXCLUDED.rating,
          review = EXCLUDED.review,
          genre = EXCLUDED.genre,
          cover_url = EXCLUDED.cover_url,
          total_pages = EXCLUDED.total_pages;
      `;

      await executeQuery(upsertQuery, [
        userRes[0].id, bookName, Number(rating), coverUrl, Number(totalPages), review, genre
      ]);

      return NextResponse.json({ success: true });
    }
    return NextResponse.json({ success: false });
  } catch (err: any) {
    return NextResponse.json({ error: err.message }, { status: 500 });
  }
}